  header .menu {
    display: flex; }
    header .menu .menuBtn {
      color: #ffa600;
      margin: 0 25px;
      cursor: pointer;
      font-weight: bold; }

#index {
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url("../images/kv.jpg");
  height: 100vh;
