      #index .container .title .t3 {
        width: 523px;
        height: 159px;
        background-image: url("../images/t-3.png");
        position: absolute;
        top: 0;
        left: 0; }

#about {
  padding: 150px 0 100px; }
  #about .logo {
    width: 110px;
    margin: 0 auto;
    display: block; }
  #about .content {
    text-align: center;
    line-height: 2em;
    text-align: center; }
  #about h3 {
    font-weight: normal;
    margin-top: 40px; }

@media screen and (max-width: 768px) {
  #product {
    padding: 0; } }

#product .container {
  padding: 50px 0; }
  #product .container .tab {
    display: flex; }
    #product .container .tab .tabItem {
      position: relative;
      width: 33.33%;
      cursor: pointer;
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat; }
      #product .container .tab .tabItem:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: 100%; }
      #product .container .tab .tabItem > * {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0; }
      #product .container .tab .tabItem:after {
        position: absolute;
        transition: all 0.3s;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        content: '';
        background-color: rgba(0, 0, 0, 0.6); }
      #product .container .tab .tabItem .nameBox {
        transition: all 0.3s;
        position: absolute;
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        letter-spacing: 0.3em; }
        #product .container .tab .tabItem .nameBox .ch {
          font-weight: bold;
          font-size: 38px;
          color: white;
          text-align: center; }
        #product .container .tab .tabItem .nameBox .en {
          color: #ffa600;
          font-weight: bold;
          font-size: 25px;
          margin-top: 10px;
          text-align: center; }
      #product .container .tab .tabItem.active .nameBox, #product .container .tab .tabItem:hover .nameBox {
        box-shadow: inset 0 0 0 13px #ffa600; }
      #product .container .tab .tabItem.active:after, #product .container .tab .tabItem:hover:after {
        background-color: rgba(0, 0, 0, 0.4); }
  #product .container .pro-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 70px; }
    @media screen and (max-width: 768px) {
      #product .container .pro-list {
        padding: 0 15px; } }
    #product .container .pro-list .space {
      width: 23.5%; }
      @media screen and (max-width: 768px) {
        #product .container .pro-list .space {
          width: 48%; } }
    #product .container .pro-list .pro-item {
      width: 23.5%;
      margin-bottom: 25px;
      display: flex; }
      #product .container .pro-list .pro-item .pro-content {
        border: 1px solid #383636;
        display: block;
        transition: all 0.3s; }
        #product .container .pro-list .pro-item .pro-content:hover {
          border: 1px solid rgba(255, 166, 0, 0.5);
          transform: scale(1.03); }
      @media screen and (max-width: 768px) {
        #product .container .pro-list .pro-item {
          width: 48%; } }
      #product .container .pro-list .pro-item .pic img {
        width: 100%; }
      #product .container .pro-list .pro-item .info {
        padding: 19px 22px; }
        #product .container .pro-list .pro-item .info h2 {
